<template>
  <div class="box02">
    <div class="head">
        <div class="headleft">
            <div class="headsign"></div>

            <div>已上市</div>
        </div>
        <div class="headright">
            <div @click="changeindex(index)" :class="{pageindexactived:index==currentindex,pageindex:index!=currentindex}" v-for="(item,index) in onepagegl.state">{{index+1}}</div>
            <img  src="../../../../../assets/add(1).png" alt="more">
        </div>
        
    </div>
    <div class="contain">
        <div v-for="(item,index) in onepagegl.state[currentindex]" class="imageitem">
            <img :src="item.img" alt="">
            <div class="imagename"><a href="">{{item.name}}</a></div>
            <div class="grade">{{item.grade}}<span>分</span></div>
        </div>
    </div>
  </div>
</template>
<script>
import {onMounted, reactive, ref} from 'vue'
import axios from 'axios'
export default {
  name: 'Box02',
  setup(){
    let pagesize=7
    let pagescum=ref(0)
    let gamelist=[]
    let currentindex=ref(0)
    let onepagegl=reactive({
      state:[
  {
    "name": "生化危机8：村庄",
    "language": "中文",
    "size": "26GB",
    "type": "动作冒险AVG",
    "cptime": "2021-05-07",
    "img": "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=410546424,888352463&fm=11&gp=0.jpg",
    "remark": "全新冒险等你挑战",
    "grade":"9.9"
  }]
    })
    onMounted(()=>{
      axios.get('https://www.fastmock.site/mock/8ab8b154b1a650f96f07f749f52fb5b8/zdTest/api/ngamelist')
      .then(res=>{
        gamelist=res.data
        gamelist.length%7!=0?pagescum=parseInt(gamelist.length/7)+1:pagescum=parseInt(gamelist.length/7)
        console.log(pagescum)
        if(pagescum>0){    
            for(let i=0;i<pagescum;i++){
                onepagegl.state[i]=gamelist.slice(pagesize*i,pagesize*(i+1))
            }
        }
        console.log(onepagegl.state)
      })
      .catch(err=>{
        console.log(err)
      })
    })
    function changeindex(index){
        currentindex.value=index
    }
   return{
     gamelist,
     onepagegl,
     currentindex,
     changeindex
    }
  }

}
</script>
<style scoped>
.box02{
    min-height: 100px;
    width: 100%;
    /* border: solid red 1px; */
    flex-direction: column;
    display: flex;
    align-items: center;
    background-color: white;
}
.head{
  margin-top: 14px;
  width: 100%;
  color: #615a5a;
  height: 33px;
  line-height: 18px;
  font-family: SimHei;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.headleft{
  justify-content: space-between;
  display: flex;
  align-items: center;
}
.headsign{
 background-color:#E54C00 ;
 width: 3.5px;
 height: 20px;
 margin-right: 8px;
  
}
.headright img{
  width: 18px;
  height: 18px;
  margin-left: 16px;
}
.headright{
    display: flex;
    align-items: center;
}
.pageindex{
    margin-right: 4px;
    width: 16px;
  height: 16px;
  background-color: #615a5a;
  color: white;
  font-size: 14px;
  line-height: 16px;
}
.pageindexactived{
    margin-right: 4px;
    width: 16px;
  height: 16px;
  color: white;
  font-size: 14px;
  line-height: 16px;
    background-color: #E54C00;
}
.contain{
    padding-top: 18px;
height: 188px;
  width: 100%;
  border: 1.8px dotted #ccc;
  display: flex;
}
.contain .imageitem:first-child{
    margin-left: 21px;
}
.imageitem{
    /* border: 1px solid; */
    height: 180px;
    width: 110px;
    position: relative;
    margin-right: 9px;
}
.imageitem img{
    width: 110px;
    height: 150px;
}
.imagename{
    text-align: center;
    height: 30px;
    line-height: 22px;
    font-size: 13px;
}
.imageitem a{
    text-decoration: none;
    color: black;
}
.imageitem a:hover{
    text-decoration: underline;
    color: #E54C00;
}
.grade{
    font-size: 15px;
    font-weight: bold;
    color: white;
    /* border: 1px solid; */
    position: absolute;
    top:0;
    background-color: #1ED699;
    height: 23px;
    width: 45px;
    line-height: 25px;
}
.grade span{
    font-size: 12px;
}
</style>