      <div class="headleft">
        <div class="headsign"></div>
        <div>软件排行榜</div>
      </div>
    </div>
    <div class="containbox">
      <div class="conitem" v-for="(item,index) in state.datalist" @mouseenter="changeleftindex(index)">
        <div class="imgicon" :class="{imgicon2:index==1,imgicon3:index==2,imgicon_more:index>=3}">{{index+1}}</div>
        <div :class="{smallit:leftcurrindex!==index,smallitactive:leftcurrindex==index}"> 
          <div class="smalltext" >
            <div><a href="">{{item.name}}</a></div>
          </div>
        </div>
        <div :class="{bigit:leftcurrindex!==index,bigitactive:leftcurrindex==index}">
         
          <img :src="item.img" alt="大标">
          <div class="bigdetail">
            <div class="bigname"><a href="">{{item.name}}</a></div>
            <div class="bigtime">{{item.type}}</div>
          </div>
        </div>
        <div :class="{download:leftcurrindex!==index,downloadactived:leftcurrindex==index}">下载</div>
