    </div>
  </div>
</template>

<script>
import { onMounted, reactive, ref } from "vue";
import axios from 'axios';
// import axios from 'axios'
export default {
  name: 'Foot3',
    setup() {
      let datalist=[];
    const state=reactive({
      datalist:[]
    })
    let leftcurrindex=ref(0);
   onMounted(()=>{
     axios.get('https://www.fastmock.site/mock/8ab8b154b1a650f96f07f749f52fb5b8/zdTest/api/newgamelist')
     .then(res=>{
       state.datalist=res.data
     })
     .catch(err=>{
       console.log(err)
     })
    })
    function changeleftindex(index) {
    leftcurrindex.value = index;
    }
    return {
      leftcurrindex,
      datalist,
      state,
      changeleftindex
    };
  },
}
</script>
