<template>
  <div class="foot1box">
    <div class="head">
      <div class="headleft">
        <div @click="activeicon(index)" :class="{hlitem:currantindex!=index,hlitemactived:currantindex==index}" v-for="(item,index) in edilist">{{item}}</div>
      </div>
      <img class="headright" src="../../body01/assets/add(1).png" alt="more">
    </div>
    <div class="footglist">
      <div v-for=" item in state.glist" class="gitem">
        <img :src="item.img" alt="image">
        <div><a href="">{{item.name}}</a></div>
      </div>
    </div>
  </div>
</template>
<script>
import {onMounted, reactive, ref} from "vue"
import axios from 'axios';
export default {
  name: 'Foot2',
  setup(){
    const state=reactive({
      glist:[]
    });
    onMounted(()=>{
      axios.get('https://www.fastmock.site/mock/8ab8b154b1a650f96f07f749f52fb5b8/zdTest/api/gamelist')
      .then(res=>{
        state.glist=res.data;
      })
      .catch(err=>{
        console.log(err);
      })
    })
    let edilist=['正式版','破解版','破解版']
    let currantindex=ref(0)
    function activeicon(index){
      currantindex.value=index
      console.log(state.glist)

    }
    return{
      edilist,
      currantindex,
      state,
      activeicon
    }
  }

}
</script>
<style scoped>
.foot1box{
    height: 100%;
    min-width: 500px;
    margin-left: 40px;
    /* border: solid red 1px; */
}
.head{
  margin-top: 4px;
  width: 100%;
  color: #615a5a;
  height: 52px;
  line-height: 18px;
  font-family: SimHei;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.headleft{
  justify-content: flex-end;
  display: flex;
  align-items: center;
}
.hlitem{
  width: 60px;
  height: 25px;
  line-height: 25px;
  font-size: 16px;
  border: 1px solid;
  border-radius: 12.5px;
  background-color:#F7F9F6;
  color: #FF9501;
  margin-right: 5px;
}
.hlitemactived{
  width: 60px;
  height: 25px;
  line-height: 25px;
  font-size: 16px;
  border: 1px solid;
  border-radius: 12.5px;
  background-color:#FF9501 ;
  color: white;
  margin-right: 5px;
}
.headright{
  width: 18px;
  height: 18px;
}
.footglist{
  width: 100%;
  /* border: 1px solid; */
  height: 450px;
  display: grid;
  grid-template-columns: repeat(5,20.8%);
  grid-template-rows: repeat(4,26.1%);

}
.gitem{
  margin-left: 2px;
  height: 90px;
  width: 70px;
  /* border: solid 1px; */
}
.gitem img{
  display: block;
  height: 70px;
  width: 70px;
  /* border: solid 1px; */
}
.gitem div{
  line-height: 20px;
  font-size: 10px;
  height: 20px;
  width: 70px;
  /* border: solid 1px; */
  overflow: hidden;
}
.gitem a{
    color: black;
  text-decoration: none;
}
.gitem a:hover{
  color: #F48E09;
  text-decoration:underline;
}
</style>