<template>
  <div @mouseenter="delInv" @mouseleave="runInv" class="banner">
    <div class="item">
      <img :src="dataList[currentIndex]">
    </div>
    <div class="page" v-if="dataList.length > 1">
      <div class="titleitem">{{titles[currentIndex]}}</div>
      <div class="listpointbox">
        <li class="shiftpoint" @click="prevIndex">&lt;</li>
        <div class="pointitem" v-for="(item,index) in dataList" @click="gotoPage(index)" :class="{'current':currentIndex == index}"></div>
        <li class="shiftpoint" @click="nextIndex">&gt;</li>
      </div>
    </div>
  </div>
</template>

<script>
import {ref,onMounted} from 'vue'
export default {
  name: 'Test',
  setup(){
    let titles=["滚动1","滚动2","滚动3"];
    let dataList=["https://s.syzs.qq.com/gameassistant/web/cfeb4fe8c7fc07fc4ebbd976f96d1d88.jpg.big.webp","https://s.syzs.qq.com/gameassistant/web/641a3ab9bc7d29b3bae0ef85aa64e0a2.jpg.big.webp","https://s.syzs.qq.com/gameassistant/web/ea214b9dfb4d61dae7622837025c385b.jpg.big.webp"];
    let currentIndex=ref(0);  //默认显示图片
    let timer=null;    //定时器
    function gotoPage(index) {
      console.log(index);
      currentIndex.value = index;
      console.log(currentIndex);
    }
    function prevIndex(){
      if(currentIndex.value == 0) {
        currentIndex.value= dataList.length - 1;
      }else{
        currentIndex.value= currentIndex.value - 1;
        
      }
    }
    function nextIndex(){
      console.log('next')
      if(currentIndex.value == dataList.length - 1) {
        currentIndex.value=0;
      }else {
        currentIndex.value= currentIndex.value + 1;
      }
    }
    //定时器
    function runInv() {
      timer = setInterval(() => {
        nextIndex()
      }, 2000)
    }
    onMounted(()=>{
      runInv()
    })
        //del定时器
    function delInv() {
      clearInterval(timer)
    }
    return{
      dataList,
      currentIndex,
      gotoPage,
      prevIndex,
      nextIndex,
      runInv,
      delInv,
      titles
    }

  } 
}
</script>
<style scoped>

    .banner {
      flex-direction: column;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      background-color:#F9F9F9;
      width: 400px;
      height: 235px;
      border-bottom: solid 1px #B8B8B8;
      margin-bottom: 21px;
    
    }
    .banner img {
      display: block;
        height: 205px;
        width:100%;
    }
    .page{
      width: 100%;
      height: 30px;
      display: flex;
      line-height: 30px;
      justify-content: space-between;
      
    }
    .titleitem{
      height: 30px;
      line-height: 30px;
      padding-left: 10px;
    }
   .shiftpoint {
        list-style: none;
        float: left;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        cursor: pointer;
        color: rgba(175, 174, 174, 0.8);
        font-size: 14px;
    }
    .listpointbox{
      width: 30%;
      display: flex;
      align-items: center;
      justify-content:space-between;

    }
    .pointitem{
      height: 10px;
      width: 10px;
      background-color: #B8B8B8;
      border-radius: 50%;
    }
    .current{
      height: 10px;
      width: 10px;
      background-color: red;
      border-radius: 50%;
    }

 

</style>
