<template>
  <div class="footbox">
    <button @click="goleft" class="lefbut"></button>
    <div ref="imagebox" class="imagebox">
        <div v-for="item in state.imglist" class="imageitem_body04">
            <img :src="item.src" alt="游戏图片">
            <span class="buttext">{{item.name}}</span>
        </div>
    </div>
    <button @click="gorit" class="rightbut"></button>
  </div>
</template>
<script>
import {ref ,onMounted,reactive} from 'vue'
import axios from 'axios'
export default {
  name: 'Head',
  setup(){
      let state=reactive({
              imglist:[]
      })
      let imagebox=ref(null)
      onMounted(()=>{
          axios.get('https://www.fastmock.site/mock/8ab8b154b1a650f96f07f749f52fb5b8/zdTest/api/scrollimgs')
          .then(res=>{
              state.imglist=res.data
              console.log(state.imglist)
          })
          .catch(err=>{
              console.log(err)
          })
      })
      function goleft(){
          imagebox.value.scrollLeft=imagebox.value.scrollLeft-140
      }
        function gorit(){
          imagebox.value.scrollLeft=imagebox.value.scrollLeft+140
      }
      return{
          imagebox,
          goleft,
          gorit,
          state
      }
  }
}
</script>
<style scoped>
.footbox{
    margin-top: 20px;
    min-height: 90px;
    /* border: solid 1px black; */
    width: 88%;
    display: flex;
}
.lefbut{
        background-image: url('./asset/left.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
    height: 90px;
    width: 38px;
    background-color: #FCFCFC;
    border: 1px solid #ddd;
}
.lefbut:active{
    background-color: #ddd;
}
.rightbut{
    background-image: url('./asset/right.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
    height: 90px;
    width: 38px;
    background-color: #FCFCFC;
    border: 1px solid #ddd;
}
.rightbut:active{
    background-color: #ddd;
}
.imagebox{
    overflow: hidden;
    margin:0;
    height: 90px;
    max-width: 1140px;
    /* border: 1px solid; */
    display: flex;
}
.imagebox > div:first-child{
    margin-left: 0px;
}
.imageitem_body04{
    margin-left: 18px;
    height: 90px;
    width: 140px;
    /* border: 1px solid; */
    position: relative;
}
.imageitem_body04 img{
    height: 90px;
    width: 140px;
}
.imageitem img{
    display: block;
    height: 90px;
    width: 140px;
    /* border: 1px solid; */
}
.buttext{
    color: white;
    display: inline-block;
    position: absolute;
    width: 140px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    margin-bottom: 10px;
    font-size: 10px;
    /* border: 1px solid; */
    top:70px;
    left: 0;
    background-color: rgba(0,0,0,0.8); ;
}
</style>
